body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif; }

body * {
  box-sizing: inherit; }

svg.icon {
  fill: currentColor;
  width: 1em;
  height: 1em; }

a:not([class]) {
  text-decoration: underline;
  color: #D72027; }

*:focus {
  outline: 1px dashed #0a0a0a; }

.no-bullet {
  padding-left: 0;
  list-style-type: none; }

.inline-list > li {
  display: inline-block; }

.clearfix::before,
.clearfix::after {
  display: table;
  content: '';
  clear: both; }

.footer {
  margin-top: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #0a0a0a;
  color: #fafafa; }

.footer a {
  color: #fafafa;
  text-decoration: none; }

.footer a:hover {
  text-decoration: underline; }

.footer-contact {
  flex-direction: column;
  justify-items: center; }

.footer-social {
  align-items: center; }

.footer-copy {
  padding: 1rem 0 0; }

.row {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 1200px; }

@media (min-width: 42em) {
  .grid {
    display: flex; } }

.grid > .column {
  display: flex;
  flex-wrap: wrap;
  flex: 1; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.column.text-right {
  justify-content: flex-end; }

.text-center {
  text-align: center; }

.float-right {
  float: right; }

/* Skip links */
.skip-link {
  position: absolute;
  top: -2rem;
  left: 2%;
  color: #fafafa; }

.admin-bar .skip-link:focus {
  top: 2rem; }

.skip-link:focus {
  top: 0;
  padding: 1rem;
  background-color: #0a0a0a; }

/* Prenav */
.prenav {
  border-top: 5px solid #D72027;
  background-color: #0a0a0a; }

.header-logo {
  display: flex;
  align-items: center;
  color: #fafafa;
  text-decoration: none;
  z-index: 1; }

.header-logo:hover .header-logo__text {
  text-decoration: underline; }

.header-logo img {
  margin: 0.25rem 1rem -0.4em 0;
  height: 5rem; }

.header-logo__text {
  font-size: 2rem;
  font-weight: bold; }

.prenav .prenav-menu {
  display: flex;
  justify-content: flex-end;
  height: 100%; }

.prenav-menu li {
  display: inline-flex;
  height: 100%; }

.prenav-menu li a {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #D72027;
  border-bottom: 1px solid transparent;
  border-left: 1px solid #0a0a0a; }

.prenav-menu li a:hover {
  background-color: #333333;
  border-bottom: 1px solid #0a0a0a; }

/* Navbar container  */
.nav-main {
  background-color: rgba(10, 10, 10, 0.8); }

.navbar {
  display: table;
  overflow: hidden;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto; }

.navbar > li {
  display: table-cell; }

.navbar ul {
  list-style-type: none;
  padding-left: 0; }

/* Links inside the navbar */
.navbar a {
  display: inline-block;
  font-size: 16px;
  color: #fafafa;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none; }

/* The dropdown container */
.dropdown {
  overflow: hidden; }

/* Dropdown button */
.dropdown .dropbtn {
  position: relative;
  margin: 0;
  border: none;
  outline: none;
  font-size: 16px; }

.dropdown .dropbtn::after {
  content: '';
  display: inline-block;
  transform: translateY(-50%);
  border-top: 5px solid #D72027;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent; }

.dropdown:hover .dropbtn::after {
  border-top-color: #fafafa; }

/* Add a red background color to navbar links on hover */
.navbar a:hover,
.menu-item:hover {
  text-decoration: underline;
  background-color: #D72027; }

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  border-color: #333333;
  border-width: 0 1px 1px 1px;
  border-style: solid;
  z-index: 1; }

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: #0a0a0a;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left; }

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd; }

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block; }

.card {
  display: flex;
  margin: 0 1rem 1rem;
  padding: 1rem;
  height: auto;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #F0F0F0;
  color: #0a0a0a;
  text-decoration: underline transparent; }

.card:hover .card__heading,
.card:hover .card__button {
  text-decoration: underline currentColor; }

.card:hover .card__icon {
  top: -0.25rem;
  transform: rotate(3deg); }

.card-icons {
  margin-top: 2.5rem;
  padding-top: 0; }

@media (min-width: 42em) {
  .card-icons {
    margin-bottom: 4rem; } }

.card__icon {
  position: relative;
  top: 0;
  margin: -2.5rem auto 0;
  padding: 1rem;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #D72027;
  color: #fafafa;
  fill: currentColor;
  transition: top 0.2s, transform 0.2s; }

.card__heading {
  margin-bottom: 1rem;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s; }

.card__content {
  margin: 0;
  padding-bottom: 2rem; }

.card__button {
  margin-top: auto;
  padding: 1rem;
  background-color: #0a0a0a;
  color: #fafafa;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s, background-color 0.2s; }

.card__button:hover {
  background-color: #D72027; }

.teacher-profile {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  width: 100%;
  max-width: 250px;
  background-color: #F0F0F0; }

.teacher-profile__content {
  display: flex;
  padding-top: 1rem; }

.teacher-profile__image {
  width: 100%; }

.teacher-profile h3,
.teacher-profile ul {
  margin: 0; }
